.blocklayouts-tools-panel-effects-settings__item {
	border: 1px solid #ddd;
	border-radius: 2px;
}

.blocklayouts-effects-settings {
	width: 100%;
	.blocklayouts-effects-settings__dropdown {
		position: relative;
		&-toggle {
			width: 100%;
			gap: 12px !important;
			padding-left: 4px !important;
		}
		&-reset {
			transition: opacity 0.1s ease-in-out;
			margin: auto 8px;
			opacity: 0;
			position: absolute;
			right: 0;
			top: 8px;
		}
		&:hover {
			.blocklayouts-effects-settings__dropdown-reset {
				opacity: 1;
			}
		}
		.blocklayouts-effects-settings__indicator {
			background-color: #f3f3f3;
			border-radius: 2px;
			display: flex;
			align-items: center;
			justify-content: center;
			height: 32px;
			width: 32px;
			padding: 0;
			&.disabled {
				background: #fff
					linear-gradient(-45deg, #0000 48%, #ddd 0, #ddd 52%, #0000 0);
				display: inline-block;
				box-shadow: inset 0 0 0 1px #0003;
			}
		}
	}
}
